// import qs from 'qs'
import axios from 'axios'

// 
var baseUrl = ''

/**
 * 
 */
class BusinessError extends Error {
  constructor (code, message, data) {
    super(message)
    this.code = code
    this.name = 'BusinessError'
    this.data = data
  }
}
/**
 * 
 */
class SystemError extends Error {
  constructor (code, message, data) {
    super(message)
    this.code = code
    this.name = 'SystemError'
    this.data = data
  }
}

// axios 
// axios.defaults.timeout = 10000
/* axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8' // 'application/json;charset=UTF-8'
axios.interceptors.request.use((config) => {
  if (config.method === 'post') {
    config.data = qs.stringify(config.data)
  }
  return config
}, (error) => {
  console.error('')
  return Promise.reject(error)
}) */
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'

//  POST 
function post (option, vm) {
  option.method = 'POST'
  return http(option, vm)
}

//  GET 
function get (option, vm) {
  option.method = 'GET'
  return http(option, vm)
}
function http (option, vm) {
  return new Promise((resolve, reject) => {
    axios({
      method: option.method,
      url: baseUrl + option.url,
      data: option.params || {t: Date.now()}
    }).then(function (res) {
      console.log(option.url + ' ', res)
      if (res.data.success || res.data.code === '0000') { // 
        resolve(res.data.data)
      } else { // 
        console.error('', res)
        let message = res.data.message || ''
        let err = new BusinessError(res.data.code, message, res.data.data)
        errorhandle(err, reject, option, vm)
      }
    }, err => {
      console.error('', err)
      errorhandle(err, reject, option, vm)
    }).catch(function (err) {
      console.error('', err)
      errorhandle(err, reject, option, vm)
    })
  })
}

/*  */
function errorhandle (err, reject, option, vm) {
  console.error(option.url, '', err.code, '#####', err)
  var error = null
  if (err.name === 'BusinessError') {
    error = err
  } else {
    error = new SystemError(500, '')
  }
  console.log('error = ', error)
  if (vm) {
    if (error.name === 'BusinessError') { // 
      // 
      if (error.code === 911) {
        vm.$toast({type: 'error', message: error.message})
        error.ignore = true
      }
    } else { // 
      vm.$toast('')
    }
  }
  reject(error)
}

export default {
  http,
  post,
  get
}



