/**
 * @file router
 * @author liu_lin_sp@126.com
 */

import Vue from 'vue'
import Router from 'vue-router'
import * as types from './store/mutation-types'
import NotFound from '@/pages/NotFound.vue'

Vue.use(Router)

// pathbuild/loaders/router-loader
const allRoutes = ['/','/banners','/','/button','/','/checkbox','/','/collapse','/','/flip','/','/home','/','/','/popup','/','/radio','/','/scroll','normal','list','horizontal','/','/switch','/','/toast']

/**
 * allRoutes
 *
 * @param {string} path 
 * @return {boolean} 
 */
function validateRoute (path) {
  return allRoutes.includes(path) ||
        allRoutes.some(route => {
          //  /detail/:id => /^\/detail\/[^\/]+\/?$/
          /* eslint-disable no-useless-escape */
          let routeRegex = new RegExp(`^${route.replace(/\/:[^\/]*/g, '/[^\/]+')}\/?$`)
          return routeRegex.test(path)
        })
}

export function createRouter ({routes = []}) {
  const router = new Router({
    // mode: 'history',
    base: '/',
    linkActiveClass: 'active',
    routes: [
      ...routes,
      {
        path: '*',
        component: NotFound,
        beforeEnter (to, from, next) {
          if (validateRoute(to.fullPath)) { // 
            window.location.href = to.fullPath
            return
          }
          next() // 404
        }
      }
    ]
  })

  return router
}



