import Vue from 'vue'
import Toast from './toast.vue'

// toast
const ToastConstructor = Vue.extend({
  extends: Toast
})
// toast
let toastPool = []

/** toast */
let getInstance = () => {
  // console.log('toastPool:', toastPool)
  if (toastPool.length > 0) {
    return toastPool.shift()
  }
  return new ToastConstructor({
    el: document.createElement('div')
  })
}

/**  */
let returnInstance = instance => {
  if (instance) {
    toastPool.push(instance)
    // console.log(':', instance, toastPool)
  }
}

/** toastDOM */
function removeDom (event) {
  if (event.target.parentNode) {
    event.target.parentNode.removeChild(event.target)
  }
}

// 
ToastConstructor.prototype.close = function () {
  this.visible = false // 
  this.closed = true // 
  this.$el.addEventListener('transitionend', removeDom) // DOM
  returnInstance(this) // 
}

// toast
export default function (options = {}) {
  // 3
  let duration = options.duration || 3000
  let instance = getInstance()
  // console.log('instance=', instance)
  // 
  instance.type = options.type || 'normal'
  // 
  instance.message = typeof options === 'string' ? options : options.message
  // topmiddlebottom
  instance.position = options.position || 'middle'
  instance.className = options.className || ''
  // 
  instance.$el.removeEventListener('transitionend', removeDom)
  // console.log('instance.$el=', instance.$el)
  // 
  document.body.appendChild(instance.$el)

  instance.visible = true
  instance.closed = false

  // 
  instance.timer && clearTimeout(instance.timer)
  // toast
  instance.timer = setTimeout(() => {
    console.log('', instance)
    !instance.closed && instance.close()
    instance.timer = null
  }, duration)
}



